import React, { useState, useEffect} from 'react';
import Players from "./Players"
import Parser from 'html-react-parser';

const url = "https://api.mcsrvstat.us/2/thickthighs.org"

// USE THIS FOR FACES
// https://crafatar.com/

function App() {
  const [numPlayers, setNumPlayers] = useState(0);
  const [status, setStatus] = useState(false);
  const [users, setUsers] = useState([])
  const [isLoading, setIsLoading] = useState(true)
  const [motd, setMotd] = useState("")

  async function getInfo () {
    const res = await fetch(url)
    const json = await res.json()

    setStatus(json.online)
    if (json.online === true) {
      setNumPlayers(json.players.online)
      setUsers(json.players.uuid)
      setMotd(json.motd.html)
    }

    setIsLoading(false);
    console.log(users)
  }

  useEffect(() => {
    getInfo(); 
  }, [])

  return !isLoading ? (
    <>
      <div className="card">
        <h3>{Parser(motd)}</h3>
        <h2>{(status) ? "Server is operational" : "Server is offline"}</h2>
        <div className="icon">
          <img src="https://cdn.discordapp.com/attachments/813939912419704833/920888728103288942/pfp.png" id="favicon" alt="" /></div>
        <div className="header">
          <div className="image"> <img src="https://cdn.discordapp.com/attachments/813939912419704833/920888729005072415/8a79ae60c65c025c65d1e9a70fdacd95.jpg" alt="" /> </div>
          <h2>thickthighs.org</h2>
        </div>
        <h1>{(numPlayers < 1) ? "" : "Player Count: " + numPlayers}</h1>
      </div>
      {(numPlayers > 0) ? <Players players={users} /> : <h2>No Players Online</h2>}
      
    </>
  ) : (
  <div className="card">
    <h2>LOADING...</h2>
  </div>);
}

export default App;
